#container {
  position: relative;
  width: 100vw;
  height: 100vh;
  font-size: 14px;
}

#slides {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0px;
  background: #000;
}

#slides .slide {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
}

#slides .slide .title {
  position: absolute;
  top: calc(50% - 0.5em);
  left: 20px;
  z-index: 2;
  padding-top: 5px;
  font-family: 'Montserrat', sans-serif;
  font-size: 5em;
  color: white;
  overflow: hidden;
}

#slides .slide .title .title__text {
  display: block;
  -webkit-transform: translateY(1.2em);
  transform: translateY(1.2em);
  transition: -webkit-transform 1s ease-in-out;
  -webkit-transition: -webkit-transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
}

#slides .slide .slide__partial {
  position: absolute;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: -webkit-transform 1s ease-in-out;
  -webkit-transition: -webkit-transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
}

#slides .slide .slide__partial img {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: -webkit-transform 1s ease-in-out;
  -webkit-transition: -webkit-transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
}

#slides .slide .slide__left {
  top: 0;
  left: 0;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
}

#slides .slide .slide__left img {
  top: 0;
  right: 0;
  object-position: 100% 50%;
  -webkit-transform: translateX(50%);
  transform: translateX(50%);
}

#slides .slide .slide__right {
  top: 0;
  right: 0;
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
  -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s;
}

#slides .slide .slide__right img {
  top: 0;
  left: 0;
  object-position: 0% 50%;
  -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
}

#slides .slide.active .title .title__text {
  -webkit-transform: translate(0);
  transform: translate(0);
  -webkit-transition-delay: 0.3s;
          transition-delay: 0.3s;
}

#slides .slide.active .slide__partial,
#slides .slide.active .slide__partial img {
  -webkit-transform: translateX(0);
  transform: translateX(0);
}

#slide__select {
  position: absolute;
  bottom: 40px;
  left: 20px;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-family: 'Montserrat', sans-serif;
  font-size: 1.6em;
  font-weight: 600;
  color: white;
  list-style: none;
}

#slide__select li {
  position: relative;
  cursor: pointer;
  margin: 0 5px;
}

#slide__select li.prev:hover {
  -webkit-transform: translateX(-2px);
  transform: translateX(-2px);
}

#slide__select li.next:hover {
  -webkit-transform: translateX(2px);
  transform: translateX(2px);
}

#slide__select .selector {
  height: 14px;
  width: 14px;
  border: 2px solid white;
  background-color: transparent;
  -webkit-transition: background-color 0.5s ease-in-out;
  transition: background-color 0.5s ease-in-out;
}

#slide__select .selector.current {
  background-color: white;
}

